
.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
  /* justify-content: center; */
  border: 2px dashed rgb(176, 92, 32);
  border-radius: 8px;
  padding: 10px 6px 20px;
  /* align-items: center; */
}

.card > img {
  width: 150px;
  height: 150px;
  object-fit: contain;

}

.card button {
  margin-top: 12px;

}


.buttonWrapper {
  display: flex;
  justify-content: center;
}
